/*********************************
3. Home
*********************************/

.home
{
	width: 100%;
	height: 100vh;
}
.home_background_container
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.home_background
{
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
}

/*********************************
3.1 Header
*********************************/

.header
{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 128px;
	background: rgba(27, 11, 51, 0.52);
	z-index: 10;
}
.header.scrolled
{
	height: 80px;
	background: rgba(27, 11, 51, 0.92);
}

/*********************************
3.1.1 Logo
*********************************/

.logo
{
	padding-left: 57px;
	flex-basis: 50%;
}
.logo a
{
	font-size: 36px;
	font-weight: 500;
	color: #fefefe;
	letter-spacing: 0.1em;
	line-height: 128px;
}
.logo a span
{
	font-weight: 800;
	color: #ff6b09;
	text-transform: uppercase;
}

/*********************************
3.1.2 Main Navigation
*********************************/

.main_nav
{
	flex-basis: 200%;
}
.main_nav ul
{
	display: inline-block;
	margin-right: 100px;
	-webkit-transform: translateY(-4px);
	transform: translateY(-4px);
}
.main_nav ul li
{
	display: inline-block;
	margin-right: 65px;
}
.main_nav ul li:last-child
{
	margin-right: 0px;
}
.main_nav ul li a
{
	display: block;
	font-size: 15px;
	font-weight: 400;
	line-height: 40.51px;
	color: #FFFFFF;
}
.main_nav ul li.active a
{
	font-weight: 700;
}

/*********************************
3.1.3 Search
*********************************/

.search
{
	display: inline-block;
	margin-right: 49px;
	width: 237px;
	height: 100%;
}
.search_content
{
	width: 100%;
	height: 100%;
}
.search_button
{
	width: 40px;
	height: 40px;
	background: transparent;
	cursor: pointer;
}
.search_button:active
{
	background: #301b54;
}
.search_form
{
	position: absolute;
	right: 0;
	top: 90px;
	width: 237px;
	height: 40px;
	visibility: hidden;
	opacity: 0;
}
.search_form.active
{
	top: 75px;
	visibility: visible;
	opacity: 1;
}
.search_input
{
	width: 100%;
	height: 100%;
	background: #FFFFFF;
	color: #1b0b32;
	padding-left: 20px;
	outline: none !important;
	border: none !important;
	box-shadow: 0px 8px 20px rgba(0,0,0,0.15);
}

/*********************************
3.1.4 Hamburger
*********************************/

.hamburger_container
{
	display: none;
	cursor: pointer;
	margin-right: 40px;
	flex-basis: 40px;
	text-align: right;
}
.hamburger_container i
{
	font-size: 24px;
	padding: 10px;
	color: #FFFFFF;
}
.hamburger_container:hover i
{
	color: #ff6b09;
}

/*********************************
3.2 Hero Slider
*********************************/

.hero_slider_container
{
	width: 100%;
	height: 100%;
	z-index: 9;
}
.hero_slider
{
	width: 100%;
	height: 100%;
}
.hero_slider_item
{
	height: 100%;
}
.item_1 span
{
	font-size: 24px;
	line-height: 1.5;
	font-weight: 500;
	color: #FFFFFF;
}
.item_1 span:first-child
{
	font-size: 24px;
	margin-bottom: 22px;
}
.item_1 span:nth-child(2)
{
	margin-bottom: 11px;
}
.item_1 span:nth-child(3)
{
	font-size: 100px;
	font-weight: 800;
	line-height: 1;
	letter-spacing: 0.1em;
	margin-bottom: 14px;
	margin-right: -0.1em;
}
.item_1 span:nth-child(4)
{
	font-size: 24px;
}
.hero_slider_nav
{
	position: absolute;
	top: 50%;
	width: 50px;
	height: 50px;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	z-index: 10;
}
.hero_slider_nav_left
{
	left: 50px;
}
.hero_slider_nav_right
{
	display: none;
	right: 50px;
}
.hero_slider_nav div
{
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background: transparent;
	cursor: pointer;
}
.hero_slider_nav div::before
{
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	content: '';
	background: #ff6b09;
	border-radius: 50%;
	-webkit-transform: scale(0);
	        transform: scale(0);
	opacity: 0;
	z-index: -1;
	transition: all 200ms ease;
}
.hero_slider_nav div i
{
	font-size: 18px;
	color: #a2baff;
}
.hero_slider_nav div:hover::before
{
	opacity: 1;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.hero_slider_nav div:hover i
{
	color: #251343;
}
.hero_side_text_container
{
	position: absolute;
	top: 0;
	right: 285px;
	width: 865px;
	height: 236px;
	background: #301b55;
	z-index: 9;
	-webkit-transform-origin: top right;
	        transform-origin: top right;
	-webkit-transform: rotate(-90deg);
	transform: rotate(-90deg);
}
.hero_side_text
{
	width: 80.5%;
	height: 100%;
	padding-right: 170px;
	padding-top: 51px;
	float: left;
}
.hero_side_text h2
{
	display: block;
	font-size: 30px;
	font-weight: 300;
	color: #9878e2;
}
.hero_side_text p
{
	display: block;
	font-size: 16px;
	font-weight: 300;
	line-height: 2.25;
	color: #FFFFFF;
}
.double_arrow_container
{
	width: 19.5%;
	height: 100%;
	float: left;
}
.double_arrow
{
	padding: 5px;
	-webkit-transform: translateY(-5px);
	transform: translateY(-5px);
	cursor: pointer;
}
.double_arrow i
{
	color: #FFFFFF;
}
.double_arrow i:first-child
{
	margin-right: -3px;
}
.double_arrow:hover i
{
	color: #ff6b09;
}

/*********************************
3.3 Next Section Scroll
*********************************/

.next_section_scroll
{
	display: none;
	position: absolute;
	left: 50%;
	bottom: 40px;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
	cursor: pointer;
	z-index: 9;
	padding: 10px;
}
.next_section i
{
	display: block;
	color: #FFFFFF;
}
.next_section i:first-child
{
	margin-bottom: -4px;
}
.next_section_scroll:hover i
{
	color: #ff6b09;
}

/*********************************
4. Menu
*********************************/

.menu_container
{
	position: fixed;
	top: 0;
	right: -400px;
	width: 400px;
	height: 100vh;
	background: #FFFFFF;
	z-index: 11;
	opacity: 0;
	transition: all 0.3s cubic-bezier(0.7, 0, 0.3, 1);
}
.menu_container.active
{
	right: 0;
	opacity: 1;
}
.menu
{
	width: 100%;
	height: 100%;
}
.menu_close
{
	position: absolute;
	top: 50px;
	right: 30px;
	cursor: pointer;
}
.menu_close i
{
	font-size: 28px;
}
.menu_close:hover i
{
	color: #ff6b09;
}
.menu ul
{
	padding-top: 115px;
	padding-right: 50px;
}
.menu ul li
{
	position: relative;
}
.menu ul li::after
{
	display: block;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	right: -10px;
	width: 5px;
	height: 5px;
	border-radius: 50%;
	background: #ff6b09;
	content: '';
}
.menu ul li.active a
{
	font-weight: 700;
}
.menu ul li a
{
	color: #000;
	transition: all 200ms ease;
}
.menu ul li a:hover
{
	color: #ff6b09;
}

/*********************************
5. Icon Boxes
*********************************/

.icon_boxes
{
	width: 100%;
	padding-top: 123px;
	padding-bottom: 129px;
}
.icon_box_title
{
	padding-right: 50px;
	
}
.icon_box_title h1
{
	line-height: 1.25;
}
.icon_box_button
{
	margin-top: 40px;
}
.icon_box_button:hover
{
	background: #452b78;
}
.icon_box_button:hover a
{
	color: #FFFFFF;
}
.icon_box_col
{
	padding-top: 21px;
}
.icon_box_item
{
	margin-bottom: 63px;
}
.icon_box_item:last-child
{
	margin-bottom: 0px;
}
.icon_box_item h2
{
	margin-bottom: 21px;
}
